$white:    #fff;
$gray-100: #f8f9fa;
$gray-200: #e9ecef;
$gray-300: #dee2e6;
$gray-400: #ced4da;
$gray-500: #adb5bd;
$gray-600: #868e96;
$gray-700: #495057;
$gray-800: #373a3c;
$gray-900: #212529;
$black:    #000;

$blue:    #2780E3;
$indigo:  #6610f2;
$purple:  #613d7c;
$pink:    #e83e8c;
$red:     #b71928;
$orange:  #F2661F;
$yellow:  #BED32B;
$green:   #3FB618;
$teal:    #20c997;
$cyan:    #9954BB;
$lightblue: lighten($blue, 20%);

$primary:       $blue;
$secondary:     $orange;
$success:       $green;
$info:          $lightblue;
$warning:       $yellow;
$danger:        $red;
$light:         $gray-100;
$dark:          $gray-800;

@import "~bootswatch/dist/cosmo/_variables.scss";
@import "~bootstrap/scss/bootstrap.scss";
@import "~bootswatch/dist/cosmo/_bootswatch.scss";
@import "~font-awesome/scss/font-awesome.scss";
@import "~@yourrequest/nijmegen-custom-components/dist/formio-contrib.css";
@import "wizardsteps";

.logo {
  height: 3.0em;
}

#main {
  margin-top: 10px;
}

formio-alerts{
  display: none;
}

.header{
  margin-top: 20px;
}

.login-link {
  display: inline-flex;
  /*height: 4.6rem;*/
  width: 28rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  cursor: pointer;
  text-decoration: none
}

.justify-content-header {
  @media only screen and (max-width: 992px) {
    justify-content: normal;
  }

  @media only screen and (min-width: 993px) {
    justify-content: space-between;
  }
}

.navbar,.navbar .container,.navbar .container-fluid,.navbar .container-lg,.navbar .container-md,.navbar .container-sm,.navbar .container-xl {
  @media only screen and (max-width: 992px) {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: normal;
  }

  @media only screen and (min-width: 993px) {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
  }
}

a {
  text-decoration: underline;

  &:focus,
  &:hover {
    text-decoration: underline;
  }


  &.no-underline,
  &.btn,
  &.card-container,
  .navbar &,
  .footer-content & {
    text-decoration: none;
  }
}
p {
  &.lead {
    margin-bottom: 3rem;
  }

  &.text-right.text-uppercase {
    text-decoration: underline;
  }
}

.col-lg-4.subpages,
.col-lg-6.subpages {
  margin-bottom: 25px;

  .card-filename .card-body .card-title {
    min-height: initial;
  }
}

.card-filename .card-body .card-title {
  font-size: 1.5rem;
  line-height: 1.8rem;
  /* visually prevent high card titles */
  max-height: 2.4em;
  min-height: 2.4em;
  overflow: hidden;
  width: 100%;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}

.whitespace {
  @media only screen and (min-width: 601px) {
    margin-top: 150px;
  }

  @media only screen and (max-width: 600px) {
    margin-top: 75px;
  }
}

.lesswhitespace {
  margin-top: 50px;
}

.page-footer {
  width: 100vw;
  margin-left: -50vw;
  left: 50%;
  position: relative;
  margin-top: 60px!important;
}

.navbar-nav .nav-item span {
  color: #fff;
  transition: padding .5s ease-in-out;
  font-size: 1.25rem;
}

.mdi-logout{
  margin-left: 10px !important;
}
.navbar-toggler-icon{
  display:inline-block;
  width:1.5em;
  height:1.5em;
  vertical-align:middle;
  content:"";
  background:no-repeat center center;
  background-size:100% 100%
}

.navbar-toggler-icon{
  background-image:url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%280, 0, 0, 0.5%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e") !important;
}

.navbar-toggler-icon{
  background-image:url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%28255, 255, 255, 0.5%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e") !important;
}

div.formio-component-checkboxnijmegen div {
  padding-left: 0;
}

label.control-label--hidden {
  left: auto;
  font-size: 1.5rem;
}

div.formio-component-fieldset legend {
  display: none;
}

div.modal{
  top: 20% !important;
}

div.modal-dialog{
  top: 20px !important;
}

.modal-header{
  border-bottom: 0px !important;
}

.modal-footer{
  border-top: 0px !important;
}

// hide the english help blocks
.help-block{
  display: none;
}

#main-content {
  min-height: 53vh;
}

button.btn-secondary:not(.summary-button):not(.formio-button-remove-row) {
  @media only screen and (max-width: 600px) {
    width: 85vw;
  }
}

.datagrid-table td {
  @media only screen and (max-width: 600px) {
    max-width: 42vw;
  }
}

.formio-button-add-row {
  @media only screen and (max-width: 600px) {
    max-width: 75vw;
  }
}


button.btn-primary:not(.summary-button):not(.formio-button-remove-row) {
  @media only screen and (max-width: 600px) {
    width: 85vw;
  }
}

.hiddenfield {
  display: none;
}

.nonhiddenfield {
  pointer-events: none;
  margin-bottom: 0;
  margin-top: 0;
}

.nonhiddenfield input {
  border-bottom: none !important;
}

.emptynonhiddenfield {
  display: none;
}

a.nav-no-link:hover{
  color: white !important;
}

.formio-component-panel {
  .mb-2.card.border {
    .card-header {
      display: none;
    }
    .card-body {
      padding: 0;
    }
    border: none!important;
  }
}

.formio-errors {
  .form-text.error {
    color: #b71928 !important;
  }
}

.field-required:after {
  color: #b71928 !important;
}

.bold {
  font-weight: 800;
}

.no-left-margin{
  margin-left: 0px !important;
}

.modal-dialog {
  position: fixed;
  z-index: 1;
  padding-top: 100px;
  left: 0;
  right: 0;
}

.mdi.mdi-calendar:focus {
  color: #157c68;
}

.form-text.text-muted {
  color: #585858!important;
}

.modal-close-button:focus {
  text-shadow: none;
  color: #ffffff;
  background-color: #157c68;
  border-radius: 0.125rem;
  box-shadow: 0 2px 5px 0 rgb(0 0 0 / 16%), 0 2px 10px 0 rgb(0 0 0 / 12%);
}

a:not(:empty) {
 color: #157c68;
}

.pdflink {
  color: #157c68!important;
  text-decoration: underline!important;
}

.formio-component-button .has-error .help-block {
  display: inline;
  color: #b71928;
  cursor: auto;
}

.modal-backdrop {
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 999;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.2);
}
