.pagination{all:unset;}

.pagination .page-item{all:unset;}

.pagination .page-item .page-link{all:unset;}

.pagination {

   display: none;

  .page-item {
    padding: .6rem;

    .page-link {
      color: #157c68;
      font-size: 1.125rem;
      height: 36px;
      line-height: 36px;
      margin: 0 1px;
      text-align: center;
      width: 36px;
      background: none;
      border-radius: 2px;
      padding: .5rem 1rem;
      cursor: pointer;

      &:hover      {
        background-color:#efefef;
        box-shadow: none;
        color: #1a6b5b;
      }

      &:focus {
        outline: 3px auto !important;
        box-shadow: none;
      }

      > [aria-hidden='true'] {
        line-height: 42px;
      }
    }

    &.disabled .page-link,
    &:disabled {
      color: #cccccc;
    }

    &.active .page-link {
      background: #157c68;
      color: #fff;

      &:hover,
      &:focus {
        outline: 1px solid #00b593!important;
        outline-offset: 2px;
        outline-width: 2px!important;
        background: #157c68 !important;
      }
    }
  }
}

.btn {
  margin: .375rem;
  color: inherit;
  text-transform: uppercase;
  word-wrap: break-word;
  white-space: normal;
  cursor: pointer;
  border: 0!important;
  border-radius: .125rem;
  box-shadow: 0 2px 5px 0 rgb(0 0 0 / 16%), 0 2px 10px 0 rgb(0 0 0 / 12%);
  -webkit-transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
  transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
  padding: .84rem 2.14rem !important;
  font-size: 1.125rem !important;
  font-weight: 600;
  letter-spacing: .7px;
}

.btn-primary{
  color: #fff;
  background-color: #157c68 !important;
}

.btn-wizard-nav-next,
.btn-wizard-nav-previous {
  background-color: #fff!important;
  border: 1px solid #ababab;
  color: #157c68!important;
}

.btn-wizard-nav-next:focus,
.btn-wizard-nav-next:hover,
.btn-wizard-nav-previous:focus,
.btn-wizard-nav-previous:hover {
  background-color: #efefef!important;
  border-color: #ababab;
  color: #17564a!important;
}

.btn-wizard-nav-next:hover,
.btn-wizard-nav-previous:hover{
  outline: 0;
  box-shadow: 1 5px 11px 0 rgb(0 0 0 / 18%), 0 4px 15px 0 rgb(0 0 0 / 15%)
}

.formio-component-columns {
  justify-content: space-between;
  button {
    width: 200px;
  }
}

.btn.btn-primary.btn-wizard-nav-submit {
  display: none;
  opacity: 0;
}


